.logo-bs{
    display: flex;
    align-items: center;
    line-height: 1em;
    text-transform: uppercase;
    color: $cWhite;
    @include rem('font-size', 36px);

    img{
        height: 50px;
        width: auto;
    }

    .logo-wrp{
        position: relative;
        top: 5px;
        margin-right: 30px;
        display: inline-block;
        width: 55px;
        overflow: hidden;
    }

}


