%red-buttons-behaviors{
    transition: transform .1s $transition, background 0.2s $transition1;

    &:hover{
        transform: translate3d(0, -1px, 0);
        background: lighten($c3, 6);
    }

    &:active {
        transform: translate3d(0, 1px, 0);
        background: darken($c3, 6);
    }

}


